import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { map, catchError } from 'rxjs/operators';
import * as sp_app_globals from '../../app/app-globles';

/*
  Generated class for the ApiManagerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ApiManagerProvider {

  public authHeaders: HttpHeaders;
  resp: Response;

  constructor(public http: HttpClient) {
    console.log('Hello ApiManagerProvider Provider');
  }

  initiateRest(url: string, params, requestBody, httpMethod: sp_app_globals.HttpMethod): Observable<{}> {

    let headers = new HttpHeaders().set('Content-Type', 'application/json');
    //headers = headers.set('Accept', 'application/json');
    headers = headers.set(sp_app_globals.k_api_key, sp_app_globals.k_api_key_value);

    if (httpMethod == sp_app_globals.HttpMethod.get) {
      console.log("url -"+url);
      return this.http.get(url, { headers, params }).pipe(
        map(this.extractData),
        catchError(this.handleError)
      );
    }
    else if (httpMethod == sp_app_globals.HttpMethod.post) {

      console.log("auth Header" + this.authHeaders);
      return this.http.post(url, requestBody, { headers }).pipe(
        map(this.extractData),
        catchError(this.handleError)
      );
    }
  }

  private extractData(res: Response) {
    let body = res;
    return body || {};
  }

  private handleError(error: Response | any) {
    let err: any;
    if (error instanceof HttpErrorResponse) {
      err = error || '';
      // const err = error || '';
      // errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      err = error.message ? error.message : error.toString();
    }
    console.error(err);
    return Observable.throw(err);
  }

}



