
import { Injectable } from '@angular/core';
import { HttpClientModule, HttpHeaders, HttpClient } from '@angular/common/http';

import * as app_globals from '../../app/app-globles';

import { ApiManagerProvider } from '../../providers/api-manager/api-manager'


/*
  Generated class for the BranchMuseumsServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class BranchMuseumsServiceProvider {
  newBranches: any = [];
  seletectlanguage: String = 'en';


  constructor(public http: HttpClient, public apiManager: ApiManagerProvider) {
  }
  public getAll() {

    let temp_url = app_globals.k_base_url + app_globals.k_url_museum_list;
    let http_body = null;
    let params = null;
    let http_method = app_globals.HttpMethod.post;

    return this.apiManager.initiateRest(temp_url, params, http_body, http_method);
  }

  public populateMuseum(language: String) {
    if (this.seletectlanguage === language && this.newBranches.length > 0) {
      console.log('Get from the List');
      return this.newBranches;
      
    } else {
      this.getAll().subscribe(
        successResponse => {
          let branches: any = successResponse;


          for (let branch in branches.museums) {
            this.getById(branches.museums[branch].id, language).subscribe(
              successResponse2 => {
                this.seletectlanguage = language;
                console.log('Populate');
                this.newBranches.push(successResponse2);
              });
          }
        });
      return this.newBranches;
    }


  }

  public getById(id: Number, language: String) {
    let temp_url = app_globals.k_base_url + app_globals.k_url_museum_by_id;
    let http_body = { "languageId": language, "museumId": id };
    let params = null;
    let http_method = app_globals.HttpMethod.post;

    return this.apiManager.initiateRest(temp_url, params, http_body, http_method);

  }
}
interface IMuseums {

  id: Number;
  name_si: String;
  name_en: String;
  name_ta: String;
  latitude: any;
  longitude: any;
  active: Number;
}


