
import { Injectable } from '@angular/core';
import { HttpClientModule, HttpHeaders, HttpClient } from '@angular/common/http';

import * as app_globals from '../../app/app-globles';

import { ApiManagerProvider } from '../../providers/api-manager/api-manager'


/*
  Generated class for the BranchMuseumsServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class GalleryServiceProvider {
  newBranches: any = [];
  seletectlanguage: String = 'en';


  constructor(public http: HttpClient, public apiManager: ApiManagerProvider) {
  }
  public getFloors() {

    let temp_url = app_globals.k_base_url + app_globals.k_url_floors;
    let http_body = {"museumId":app_globals.k_default_museum_id};
    let params = null;
    let http_method = app_globals.HttpMethod.post;

    return this.apiManager.initiateRest(temp_url, params, http_body, http_method);
  }
public getItemByFloor(floorId: Number,language: String) {
    let temp_url = app_globals.k_base_url + app_globals.k_url_item_by_floor;
    let http_body = {"languageId":language,"museumId":app_globals.k_default_museum_id,"floorId":floorId}
    let params = null;
    let http_method = app_globals.HttpMethod.post;

    return this.apiManager.initiateRest(temp_url, params, http_body, http_method);

  }
 

  public getItemById( itemId: Number,language: String) {
    let temp_url = app_globals.k_base_url + app_globals.k_url_item_by_id;
    let http_body = {"languageId":language,"museumId":app_globals.k_default_museum_id,"itemId":itemId};
    let params = null;
    let http_method = app_globals.HttpMethod.post;

    return this.apiManager.initiateRest(temp_url, params, http_body, http_method);

  }
}



