import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpClientModule, HttpHeaders, HttpClient } from '@angular/common/http';
import { MyApp } from './app.component';
import { NativeAudio } from '@ionic-native/native-audio';
import { ApiManagerProvider } from '../providers/api-manager/api-manager';
import { BranchMuseumsServiceProvider } from '../providers/branch-museums-service/branch-museums-service';
import { GalleryServiceProvider } from '../providers/gallery/gallery';
import { HomeTabPage } from '../pages/home-tab/home-tab';
import { CategoryPage } from '../pages/category/category';
import { ItemPage } from '../pages/item/item';
@NgModule({
  declarations: [
    MyApp,
    HomeTabPage,
    CategoryPage,
    ItemPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomeTabPage,
    CategoryPage,
    ItemPage
  ],
  providers: [
    StatusBar,
    NativeAudio,
    SplashScreen,
    HttpClient,
    ApiManagerProvider,
    BranchMuseumsServiceProvider,
    GalleryServiceProvider,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



