import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NativeAudio } from '@ionic-native/native-audio';
import { HomeTabPage } from '../pages/home-tab/home-tab';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = HomeTabPage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen,public nativeAudio:NativeAudio) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      this.nativeAudio.preloadSimple('music', 'assets/music/background.mp3').then( function(msg){
      
        this.nativeAudio.loop( 'music' );

      },  function(msg){
      });
  
    });
  }
}




